import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as propertiesActions from "../../actions/propertiesActions";
import * as navigationTitleActions from "../../actions/navigationTitleActions";
import * as compsActions from "../../actions/compsActions";
import MyPropertiesPage from "./MyPropertiesPage";
import { Navigation } from "../../lib";
import Expire from "../common/Expire";
import Form from "muicss/lib/react/form";
import Button from "muicss/lib/react/button";
import Modal from "react-modal";
import NoteModal from "../common/NoteModal";
import MyPropertiesHeader from "./MyPropertiesHeader";
import getParameterByName from "../common/getParameterByName";
// import encodeQueryData from "../common/encodeQueryData";
import { parse, stringify } from "query-string";
import Input from "muicss/lib/react/input";

class MyPropertiesContainer extends React.Component {
  constructor(props, context) {
    super(props, context);
    this.state = {
      activeCategory: null,
      activeFilter: null,
      contactId: null,
      scrolled: false,
      count: "",
      modalIsAddOpen: false,
      modalIsEditOpen: false,
      property_id: "",
    };
  }

  componentDidMount() {
    this.navChange("My Properties");
    this.setState(
      {
        activeCategory: this.props.match.params.category,
        activeFilter: getParameterByName("filter"),
        contactId: getParameterByName("contact_id"),
      },
      () => {
        this.props.actions.loadProperties(
          this.state.activeCategory,
          this.state.activeFilter,
          this.state.contactId
        );
      }
    );
  }

  componentDidUpdate() {
    if (
      this.props.match.params.category !== this.state.activeCategory ||
      getParameterByName("filter") !== this.state.activeFilter
    ) {
      this.componentDidMount();
    }
    if (
      !this.props.properties.loading &&
      this.props.properties.properties.mine &&
      !this.state.scrolled
    ) {
      window.scrollTo(
        0,
        this.props.comps.previousScrollPos[
          this.props.comps.previousScrollPos.length - 1
        ]
      );
      setTimeout(() => {
        this.props.compsActions.resetScrollPosition();
      }, 3000);
      this.setState({
        scrolled: true,
      });
    }

    ////dont seem to be using it
    // if (this.state.count != this.props.properties.count) {
    //   this.setState({
    //     count: this.props.properties.count
    //   })
    // }
  }

  dealClick(dealType, dealId, violations, isEquity, deal_name, page) {
    this.props.compsActions.setPageAndScrollPos(
      window.location.pathname + window.location.search,
      window.scrollY
    );

    this.state.contactId
      ? Navigation.go(
          `/broker-deals/${dealType}?${stringify({
            deal_id: dealId,
            ...parse(window.location.search),
          })}`
        )
      : Navigation.go(
          `/properties/${dealType}/${dealId}/${page}?${stringify({
            ...parse(window.location.search),
            violations,
            isEquity,
            previous_page: window.location.pathname + window.location.search,
          })}`
        );
  }

  compsClick(inNYC, address, city_state_zip, property_type) {
    sessionStorage.setItem("comps_inNYC", inNYC);
    this.props.compsActions.setPageAndScrollPos(
      window.location.pathname + window.location.search,
      window.scrollY
    );
    this.props.compsActions.submitCompsSearch(
      `${address} ${city_state_zip}`,
      property_type
    );
  }

  navChange = (title) => {
    this.props.navActions.updateNavTitle(title);
  };

  removeProperty(removal_type, removal_id) {
    this.setState({
      modalOpen: true,
      removal_type: removal_type,
      removal_id: removal_id,
    });
  }

  closeRemovePropertyDialog(e) {
    e.preventDefault();
    this.setState({ modalOpen: false, removal_type: "", removal_id: "" });
  }

  currentOwnerChange(e) {
    this.setState({ currentOwner: e.target.value });
  }

  submitRemovePropertyDialog(e) {
    e.preventDefault();
    this.setState({ modalOpen: false });
    this.props.actions.removeProperty(
      this.state.removal_type,
      this.state.removal_id,
      this.state.currentOwner
    );
  }

  openAddNoteModal(removal_id, removal_type, property_id) {
    this.setState({
      removal_id: removal_id,
      removal_type: removal_type,
      property_id: property_id,
      modalIsAddOpen: true,
      note: "",
    });
  }

  openEditNoteModal(removal_id, removal_type, property_id, note) {
    this.setState({
      removal_id: removal_id,
      removal_type: removal_type,
      property_id: property_id,
      modalIsEditOpen: true,
      note: note,
    });
  }

  closeAddNoteModal() {
    this.setState({ modalIsAddOpen: false });
  }

  closeEditNoteModal(e) {
    e.preventDefault();
    this.setState({ modalIsEditOpen: false });
  }

  updateNote(removal_id, removal_type, note) {
    this.props.actions.updateNote(removal_id, removal_type, note);
  }

  handleAddNoteSubmit(e, note) {
    e.preventDefault();
    this.setState({ modalIsAddOpen: false });
    this.updateNote(this.state.removal_id, this.state.removal_type, note);
  }

  handleUpdateNoteSubmit(e, note) {
    e.preventDefault();
    this.setState({ modalIsEditOpen: false });
    this.updateNote(this.state.removal_id, this.state.removal_type, note);
  }

  render() {
    let successMessage = null;
    if (this.props.properties.errorToast) {
      successMessage = (
        <Expire closeToast={this.props.actions.closeErrorToast} delay={6000}>
          <div className="success-message">
            <p>{this.props.properties.message}</p>
            <Button variant="flat" onClick={this.props.actions.closeErrorToast}>
              Done
            </Button>
          </div>
        </Expire>
      );
    }
    let addText = {
      title: "Add new note",
      button: "Save",
    };
    let editText = {
      title: "Edit note",
      button: "Update",
    };
    return (
      <div>
        {successMessage}
        <MyPropertiesHeader
          queryString={window.location.search}
          previousPage={getParameterByName("previous_page")}
          searchValue={this.props.properties.searchValue}
          updateSearch={this.props.actions.updateSearch}
        />
        <MyPropertiesPage
          properties={this.props.properties.searchedProperties.mine}
          linkedProperties={this.props.properties.searchedProperties.linked}
          filter={this.state.activeFilter}
          category={this.state.activeCategory}
          loading={this.props.properties.loading}
          noProperties={this.props.properties.noProperties}
          navChange={this.navChange.bind(this)}
          updateNote={this.updateNote.bind(this)}
          dealClick={this.dealClick.bind(this)}
          compsClick={this.compsClick.bind(this)}
          removeProperty={this.removeProperty.bind(this)}
          setPageAndScrollPos={this.props.compsActions.setPageAndScrollPos}
          submitCompsSearch={this.props.compsActions.submitCompsSearch}
          updateErrorToast={this.props.actions.updateErrorToast}
          openAddNoteModal={this.openAddNoteModal.bind(this)}
          openEditNoteModal={this.openEditNoteModal.bind(this)}
        />

        <Modal
          className="note-modal snooze-modal"
          isOpen={this.state.modalOpen}
          ariaHideApp={false}
          contentLabel="Modal"
          onAfterOpen={function () {}}
          onRequestClose={this.closeRemovePropertyDialog.bind(this)}
          shouldCloseOnOverlayClick={true}
        >
          <Form className="modal-form">
            <p className="modal-title">Remove Property</p>
            <p className="modal-subtitle">
              Who is the current owner? (optional)
            </p>
            <Input onChange={this.currentOwnerChange.bind(this)} />
            <div className="add-note-buttons">
              <Button
                variant="flat"
                onClick={this.closeRemovePropertyDialog.bind(this)}
              >
                CANCEL
              </Button>
              <Button
                variant="flat"
                onClick={this.submitRemovePropertyDialog.bind(this)}
                type="submit"
                value="submit"
              >
                OK
              </Button>
            </div>
          </Form>
        </Modal>

        <NoteModal
          isOpen={this.state.modalIsAddOpen}
          onRequestClose={this.closeAddNoteModal.bind(this)}
          onAfterOpen={function () {}}
          text={addText}
          require={false}
          onSubmit={this.handleAddNoteSubmit.bind(this)}
        />

        <NoteModal
          isOpen={this.state.modalIsEditOpen}
          onRequestClose={this.closeEditNoteModal.bind(this)}
          onAfterOpen={function () {}}
          text={editText}
          require={false}
          note={this.state.note}
          onSubmit={this.handleUpdateNoteSubmit.bind(this)}
        />
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    properties: state.properties,
    comps: state.comps,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators(propertiesActions, dispatch),
    navActions: bindActionCreators(navigationTitleActions, dispatch),
    compsActions: bindActionCreators(compsActions, dispatch),
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(MyPropertiesContainer);
