import React, { Fragment } from "react";
import Modal from "react-modal";
import PWDeal from "./DealPW";
import IPDeal from "./DealIP";
import RPDeal from "./DealRP";
import ReonomyDeal from "./DealReonomy";
import FilterBar from "./FilterBar";

class MyPropertiesPage extends React.Component {
  render() {
    let activeDeals = null;
    let linkedActiveDeals = null;

    if (this.props.properties) {
      activeDeals = this.props.properties.map(
        function (item, index) {
          if (item.type === "current_ip") {
            return (
              <IPDeal
                key={index}
                item={item}
                updateNote={this.props.updateNote}
                dealClick={this.props.dealClick}
                compsClick={this.props.compsClick}
                removeProperty={this.props.removeProperty}
                openStreetView={this.props.openStreetView}
                updateErrorToast={this.props.updateErrorToast}
                openAddNoteModal={this.props.openAddNoteModal}
                openEditNoteModal={this.props.openEditNoteModal}
                setPageAndScrollPos={this.props.setPageAndScrollPos}
              />
            );
          } else if (item.type === "current_pw" || item.type === "current_uw") {
            return (
              <PWDeal
                key={index}
                item={item}
                dealClick={this.props.dealClick}
                compsClick={this.props.compsClick}
                removeProperty={this.props.removeProperty}
                openStreetView={this.props.openStreetView}
                updateErrorToast={this.props.updateErrorToast}
                openAddNoteModal={this.props.openAddNoteModal}
                openEditNoteModal={this.props.openEditNoteModal}
                setPageAndScrollPos={this.props.setPageAndScrollPos}
              />
            );
          } else if (item.type === "remaining") {
            return (
              <RPDeal
                key={index}
                item={item}
                navChange={this.props.navChange}
                updateNote={this.props.updateNote}
                removeProperty={this.props.removeProperty}
                openAddNoteModal={this.props.openAddNoteModal}
                openEditNoteModal={this.props.openEditNoteModal}
                setPageAndScrollPos={this.props.setPageAndScrollPos}
                submitCompsSearch={this.props.submitCompsSearch}
                openStreetView={this.props.openStreetViewRemaining}
                updateErrorToast={this.props.updateErrorToast}
              />
            );
          } else if (item.type === "national") {
            return (
              <ReonomyDeal
                key={index}
                item={item}
                removeProperty={this.props.removeProperty}
                updateErrorToast={this.props.updateErrorToast}
                openAddNoteModal={this.props.openAddNoteModal}
                openEditNoteModal={this.props.openEditNoteModal}
                openStreetView={this.props.openStreetViewReonomy}
                setPageAndScrollPos={this.props.setPageAndScrollPos}
              />
            );
          }
        }.bind(this)
      );

      linkedActiveDeals = this.props.linkedProperties.map(
        function (item, index) {
          if (item.type === "current_ip") {
            return (
              <IPDeal
                key={index}
                item={item}
                type="linked"
                dealClick={this.props.dealClick}
                updateNote={this.props.updateNote}
                compsClick={this.props.compsClick}
                updateErrorToast={this.props.updateErrorToast}
                setPageAndScrollPos={this.props.setPageAndScrollPos}
              />
            );
          } else if (item.type === "current_pw") {
            return (
              <PWDeal
                key={index}
                item={item}
                type="linked"
                dealClick={this.props.dealClick}
                compsClick={this.props.compsClick}
                updateErrorToast={this.props.updateErrorToast}
                setPageAndScrollPos={this.props.setPageAndScrollPos}
              />
            );
          } else if (item.type === "remaining") {
            return (
              <RPDeal
                key={index}
                item={item}
                type="linked"
                navChange={this.props.navChange}
                updateNote={this.updateNote}
                setPageAndScrollPos={this.props.setPageAndScrollPos}
                submitCompsSearch={this.props.submitCompsSearch}
                updateErrorToast={this.props.updateErrorToast}
              />
            );
          } else if (item.type === "national") {
            return (
              <ReonomyDeal
                key={index}
                item={item}
                type="linked"
                updateErrorToast={this.props.updateErrorToast}
                setPageAndScrollPos={this.props.setPageAndScrollPos}
              />
            );
          }
        }.bind(this)
      );
    }
    return (
      <Fragment>
        <div className="my-properties-container" ref="page">
          <div
            className={
              this.props.category === "deals"
                ? "filter-bar-container"
                : "hidden"
            }
          >
            <FilterBar
              category={this.props.category}
              filter={this.props.filter}
            />
          </div>
          {activeDeals}
          <div
            className={
              !this.props.loading &&
              this.props.linkedProperties &&
              this.props.linkedProperties.length
                ? "properties-quote-subheader"
                : "hidden"
            }
          >
            <hr />
            <p>Linked Properties</p>
          </div>
          {linkedActiveDeals}
        </div>
        <div className={this.props.noProperties ? "" : "hidden"}>
          <div>
            <div className="grey-circle">
              <i className="material-icons">warning</i>
            </div>
            <h2 className="no-results">No Properties Now</h2>
          </div>
        </div>
        <Modal
          className="loading-modal"
          isOpen={this.props.loading}
          ariaHideApp={false}
          contentLabel="Modal"
          shouldCloseOnOverlayClick={false}
        >
          <div className={this.props.loading ? "centered-spinner" : "hidden"}>
            <img
              src="/images/icons/spin.svg"
              className="loading-spinner"
              alt="loading spinner"
            />
          </div>
        </Modal>
      </Fragment>
    );
  }
}

export default MyPropertiesPage;
