import React from "react";
import Row from "muicss/lib/react/row";
import { Link, NavLink } from "react-router-dom";

class MyPropertiesHeader extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      showSearchbar: false,
    };
  }
  updateSearch(e) {
    this.props.updateSearch(e.target.value);
  }

  render() {
    const isMobile =
      window.isNativeApp ||
      window.matchMedia("(display-mode: standalone)").matches ||
      window.innerWidth < 901;
    return (
      <Row
        className={`sub-header ${
          this.state.showSearchbar ? "mobile-header-searchbar" : ""
        }`}
      >
        <Link
          className={this.props.previousPage ? "back-arrow" : "hidden"}
          to={decodeURIComponent(this.props.previousPage)}
        >
          <img src="/images/icons/arrow-left.png" alt="arrow-left" />
        </Link>
        <div className="nav-search-sub-header my-properties-subheader-container">
          <div>
            {/* <div> */}
            <ul>
              <li>
                <NavLink
                  to={{
                    pathname: `/properties/deals`,
                    search: this.props.queryString,
                  }}
                  activeClassName="header-active-link"
                >
                  Active Deals
                </NavLink>
              </li>
              <li>
                <NavLink
                  to={{
                    pathname: `/properties/portfolio`,
                    search: this.props.queryString,
                  }}
                  activeClassName="header-active-link"
                >
                  Portfolio
                </NavLink>
              </li>
              <li>
                <NavLink
                  to={{
                    pathname: `/properties/reonomy`,
                    search: this.props.queryString,
                  }}
                  activeClassName="header-active-link"
                >
                  Public Data
                </NavLink>
              </li>
            </ul>
            {/* </div> */}
            {!isMobile ? (
              <div className="search-container">
                <div className="search-form">
                  <i className="material-icons white search-icon">search</i>
                  <input
                    className="input contact-input"
                    type="text"
                    placeholder="Search"
                    onChange={(e) => this.updateSearch(e)}
                    value={this.props.searchValue}
                    ref="searchBox"
                  />
                </div>
              </div>
            ) : (
              <div>
                <img
                  id="search-icon-small"
                  src="/images/icons/search.png"
                  alt="search"
                  style={{ float: "right" }}
                  onClick={() =>
                    this.setState({ showSearchbar: !this.state.showSearchbar })
                  }
                />
              </div>
            )}
          </div>
        </div>

        <Row style={{ width: "96%" }}>
          <div className="flex flex-center">
            <div className="search-container search-container-mobile">
              <div className="search-form">
                <img
                  id="search-icon-small"
                  src="/images/icons/search.png"
                  alt="search"
                />
                <input
                  className="input contact-input"
                  type="text"
                  placeholder="Search"
                  onChange={(e) => this.updateSearch(e)}
                  value={this.props.searchValue}
                  ref="searchBox"
                />
              </div>
            </div>
          </div>
        </Row>
      </Row>
    );
  }
}

export default MyPropertiesHeader;
