import * as types from "./actionTypes";
import config from "../config";
import { ajax } from "../lib";
import { tokenExpired } from "./authenticationActions";

export function loadProperties(category, filter, contact_id) {
  if (category === "portfolio") {
    category = "remaining";
  }

  let data = {};

  if (filter) {
    data["filter"] = filter;
  }
  if (contact_id) {
    data["contact_id"] = contact_id;
  }

  return (dispatch) => {
    dispatch({ type: types.LOADING_PROPERTIES });
    ajax({
      url: `${config.BASE_URL}my-portfolio/${category}`,
      applyAuthHeader: true,
      data: data,
      success: function (response) {
        switch (response.status) {
          case "204":
            dispatch({ type: types.NO_PROPERTIES, message: response.message });
            break;
          case "E403":
            dispatch(tokenExpired());
            break;
          default:
            response.data && response.data.count > 0
              ? dispatch({
                  type: types.LOAD_PROPERTIES_SUCCESS,
                  data: response.data,
                  count: response.data.count,
                })
              : dispatch({
                  type: types.NO_PROPERTIES,
                  message: response.message,
                });
        }
      },
    });
  };
}

export function loadContactProperties(contact_id) {
  return (dispatch) => {
    dispatch({ type: types.LOADING_PROPERTIES });
    ajax({
      url: `${config.BASE_URL}my-portfolio`,
      applyAuthHeader: true,
      data: { contact_id },
      success: function (response) {
        switch (response.status) {
          case "204":
            dispatch({ type: types.NO_PROPERTIES, message: response.message });
            break;
          case "E403":
            dispatch(tokenExpired());
            break;
          default:
            response.data.count > 0
              ? dispatch({
                  type: types.LOAD_PROPERTIES_SUCCESS,
                  data: response.data,
                  count: response.data.count,
                })
              : dispatch({
                  type: types.NO_PROPERTIES,
                  message: response.message,
                });
        }
      },
    });
  };
}

export function updateErrorToast(error) {
  return { type: types.UPDATE_ERROR_TOAST, message: error };
}

export function closeErrorToast() {
  return { type: types.CLOSE_ERROR_TOAST };
}

export function removeProperty(removal_type, removal_id, current_owner) {
  return (dispatch) => {
    ajax({
      url: `${config.BASE_URL}remove-property`,
      applyAuthHeader: true,
      type: "POST",
      data: { removal_type, removal_id, current_owner },
      success: function (response) {
        if (response.status === "E408" || response.status === "E107") {
          dispatch({
            type: types.REMOVE_PROPERTY_FAILED,
            message: response.message,
          });
        } else if (response.status === "E403") {
          dispatch(tokenExpired());
        } else if (response.status === "206") {
          dispatch({
            type: types.REMOVE_PROPERTY_SUCCESS,
            removal_type: removal_type,
            removal_id: removal_id,
          });
        }
      },
    });
  };
}

export function updateNote(removal_id, removal_type, notes) {
  return (dispatch) => {
    ajax({
      url: `${config.BASE_URL}update-portfolio-notes`,
      applyAuthHeader: true,
      type: "POST",
      data: {
        removal_id: removal_id,
        removal_type: removal_type,
        notes: notes,
      },
      success: function (response) {
        switch (response.status) {
          case "E408":
          case "E107":
            dispatch({
              type: types.UPDATE_PROPERTY_NOTE_FAILED,
              message: response.message,
            });
            break;
          case "E403":
            dispatch(tokenExpired());
            break;
          default:
            dispatch({
              type: types.UPDATE_PROPERTY_NOTE_SUCCESS,
              removal_id: removal_id,
              removal_type: removal_type,
              notes: notes,
            });
        }
      },
    });
  };
}

export function updateSearch(searchValue) {
  return { type: types.UPDATE_PROPERTY_SEARCH, searchValue };
}
