import React from "react";
import encodeQueryData from "../common/encodeQueryData";
import { Navigation } from "../../lib";

class FilterBar extends React.Component {
  constructor(props) {
    super(props);

    this.onFilterChange = this.onFilterChange.bind(this);
  }

  onFilterChange(e) {
    let queryString = window.location.search || "";

    if (queryString.includes("filter=")) {
      let index = queryString.indexOf("filter=") - 1;
      queryString = queryString.slice(0, index);
    }

    if (e.target.value) {
      queryString += queryString === "" ? "?" : "&";
      queryString += `${encodeQueryData({ filter: e.target.value })}`;
    }
    Navigation.go(window.location.pathname + queryString);
  }

  render() {
    return (
      <div className="filter-bar">
        <input
          type="radio"
          id="allDeals"
          name="deals"
          value=""
          checked={!this.props.filter}
          onChange={this.onFilterChange}
        />
        <label htmlFor="allDeals">All</label>
        <input
          type="radio"
          id="debt"
          name="deals"
          value="debt"
          checked={this.props.filter === "debt"}
          onChange={this.onFilterChange}
        />
        <label htmlFor="debt">Debt</label>

        <input
          type="radio"
          id="equity"
          name="deals"
          value="equity"
          checked={this.props.filter === "equity"}
          onChange={this.onFilterChange}
        />
        <label htmlFor="equity">Equity</label>
      </div>
    );
  }
}

export default FilterBar;
