import React from "react";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import { Navigation } from "../../lib";
import moment from "moment";
import encodeQueryData from "../common/encodeQueryData";
const google = window.google;

export default class ReonomyProperty extends React.Component {
  openStreetView(lat, long, address, city, property_state) {
    var sv = new google.maps.StreetViewService();
    sv.getPanorama(
      {
        location: {
          lat: parseFloat(lat),
          lng: parseFloat(long),
        },
        radius: 50,
      },
      (data, status) => {
        if (status === "OK") {
          this.props.setPageAndScrollPos(
            window.location.pathname,
            window.scrollY
          );
          Navigation.go(
            `/comp-sv?${encodeQueryData({
              latitude: lat,
              longitude: long,
              address: address + " " + city + " " + property_state,
            })}`
          );
        } else {
          this.props.updateErrorToast(
            "No street view available for this property"
          );
        }
      }
    );
  }

  render() {
    return (
      <div ref="deal">
        <div className="panel mui-panel reonomy-panel">
          <Row>
            <Col md="12">
              <Row className="panel-heading">
                <Col md="10" sm="10" xs="10">
                  <p className="panel-info-value property-title">
                    {this.props.item.address ? this.props.item.address : ""}
                  </p>
                  <p className="panel-city-state">
                    <span className="bulletpoint">&bull;</span>
                    {this.props.item.city ? this.props.item.city : ""}
                    {this.props.item.city && this.props.item.property_state
                      ? ", "
                      : ""}
                    {this.props.item.property_state
                      ? this.props.item.property_state
                      : ""}
                  </p>
                </Col>
                <Col md="2" sm="2" xs="2">
                  <i
                    className={
                      this.props.type === "linked" ||
                      window.location.pathname.includes("contact")
                        ? "hidden"
                        : "material-icons deal-delete"
                    }
                    onClick={() => {
                      this.props.removeProperty(
                        this.props.item.removal_type,
                        this.props.item.removal_id
                      );
                    }}
                  >
                    delete
                  </i>
                  <img
                    src="/images/icons/street-view-square.png"
                    className="street-view-square"
                    alt="sv-square"
                    onClick={() => {
                      this.openStreetView(
                        this.props.item.latitude,
                        this.props.item.longitude,
                        this.props.item.address,
                        this.props.item.city,
                        this.props.item.property_state
                      );
                    }}
                  />
                </Col>
              </Row>
            </Col>
          </Row>
          <Row>
            <Col md="12">
              <Row>
                <Col md="3" sm="6">
                  <p className="small-title">Property Type</p>
                  <p className="panel-info-value">
                    {this.props.item.property_type
                      ? this.props.item.property_type
                      : " - "}
                  </p>
                </Col>
                <Col md="3" sm="6">
                  <p className="small-title">Mortgage Lender</p>
                  <p className="panel-info-value">
                    {this.props.item.mortgage_lender_names
                      ? this.props.item.mortgage_lender_names
                      : " - "}
                  </p>
                </Col>
                <Col md="3" sm="6">
                  <p className="small-title">Mortgage Date</p>
                  <p className="panel-info-value">
                    {this.props.item.mortgage_date
                      ? this.props.item.mortgage_date
                      : " - "}
                  </p>
                </Col>
                <Col md="3" sm="6">
                  <p className="small-title">Mortgage Amount</p>
                  <p className="panel-info-value">
                    {this.props.item.mortgage_amount
                      ? this.props.item.mortgage_amount
                      : " - "}
                  </p>
                </Col>
              </Row>
              <Row>
                <Col md="3" sm="6">
                  <p className="small-title">Units</p>
                  <p className="panel-info-value">
                    {this.props.item.units ? this.props.item.units : " - "}
                  </p>
                </Col>
                <Col md="3" sm="6">
                  <p className="small-title">Sq Ft</p>
                  <p className="panel-info-value">
                    {this.props.item.sq_ft ? this.props.item.sq_ft : " - "}
                  </p>
                </Col>
                <Col md="3" sm="6">
                  <p className="small-title">Last Sale Date</p>
                  <p className="panel-info-value">
                    {this.props.item.most_recent_sale_date
                      ? this.props.item.most_recent_sale_date
                      : " - "}
                  </p>
                </Col>
                <Col md="3" sm="6">
                  <p className="small-title">Last Sale Price</p>
                  <p className="panel-info-value">
                    {this.props.item.most_recent_sale_price
                      ? this.props.item.most_recent_sale_price
                      : " - "}
                  </p>
                </Col>
              </Row>
              {JSON.parse(localStorage.getItem("data")).is_client &&
              !window.location.pathname.includes("contact") ? (
                <Row
                  className={
                    this.props.type === "linked" ? "hidden" : "note-container"
                  }
                >
                  <Col md="10">
                    <p className="small-title deal-info-title">Note</p>
                    <div>
                      {this.props.item.notes ? (
                        <div
                          onClick={() =>
                            this.props.openEditNoteModal(
                              this.props.item.removal_id,
                              this.props.item.removal_type,
                              this.props.item.property_id,
                              this.props.item.notes
                            )
                          }
                        >
                          <p className="note">
                            {this.props.item.notes}&nbsp;
                            <img
                              src="/images/pencil.png"
                              id="pencil"
                              alt="pencil"
                            />
                          </p>
                          <p className="last-update">
                            <i>
                              last updated on{" "}
                              {moment(
                                new Date(this.props.item.notes_edited_on)
                              ).format("l")}
                            </i>
                          </p>
                        </div>
                      ) : (
                        <div
                          onClick={() =>
                            this.props.openAddNoteModal(
                              this.props.item.removal_id,
                              this.props.item.removal_type,
                              this.props.item.property_id
                            )
                          }
                          className="add-note"
                        >
                          <em>Add Note &nbsp;</em>
                          <i className="material-icons">add</i>
                        </div>
                      )}
                    </div>
                  </Col>
                </Row>
              ) : (
                ""
              )}
            </Col>
          </Row>
        </div>
      </div>
    );
  }
}
