import React from "react";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Divider from "muicss/lib/react/divider";
import { Navigation } from "../../lib";
import moment from "moment";
import { formatTotalComps } from "../../lib";
import encodeQueryData from "../common/encodeQueryData";

const google = window.google;

export default class DealIP extends React.Component {
  compsClick() {
    if (window.location.pathname.includes("contact")) {
      this.props.dealClick(
        "ip",
        this.props.item.deal_id,
        this.props.item.total_violations,
        this.props.item.is_equity,
        this.props.item.deal_name,
        "status-summary"
      );
    } else {
      this.props.compsClick(
        this.props.item.inNYC,
        this.props.item.property_address,
        this.props.item.city_state_zip,
        this.props.item.property_type_id
      );
    }
  }

  openStreetView(lat, long, property_address, city_state_zip) {
    var sv = new google.maps.StreetViewService();
    sv.getPanorama(
      {
        location: {
          lat: parseFloat(lat),
          lng: parseFloat(long),
        },
        radius: 50,
      },
      (data, status) => {
        if (status === "OK") {
          this.props.setPageAndScrollPos(
            window.location.pathname,
            window.scrollY
          );
          Navigation.go(
            `/comp-sv?${encodeQueryData({
              latitude: lat,
              longitude: long,
              address: property_address + " " + city_state_zip,
            })}`
          );
        } else {
          this.props.updateErrorToast("No street view available for this deal");
        }
      }
    );
  }

  render() {
    return (
      <div className="active-deals-pw" ref="deal">
        <div className="panel mui-panel">
          <div>
            <Row className="panel-heading">
              <Col
                md="10"
                sm="10"
                xs="9"
                onClick={() => {
                  this.props.dealClick(
                    "ip",
                    this.props.item.deal_id,
                    this.props.item.total_violations,
                    this.props.item.is_equity,
                    this.props.item.deal_name,
                    "status-summary"
                  );
                }}
              >
                <div>
                  <p className="panel-info-value property-title">
                    <b>
                      {this.props.item.deal_name
                        ? this.props.item.deal_name
                        : " - "}
                    </b>
                    <span
                      className={
                        this.props.item.deal_id
                          ? "deal-tag primary-tag"
                          : "hidden"
                      }
                    >
                      {this.props.item.is_equity ? "Equity" : "Debt"}
                    </span>
                    <span
                      className="deal-tag"
                      style={{ background: this.props.item.tag_color }}
                    >
                      {this.props.item.new_tag}
                    </span>
                  </p>
                </div>
              </Col>
              <Col md="2" sm="2" xs="3">
                <i
                  className={
                    this.props.type === "linked" ||
                    window.location.pathname.includes("contact")
                      ? "hidden"
                      : "material-icons deal-delete"
                  }
                  onClick={() => {
                    this.props.removeProperty(
                      this.props.item.removal_type,
                      this.props.item.removal_id
                    );
                  }}
                >
                  delete
                </i>
                <img
                  src="/images/icons/street-view-square.png"
                  className="street-view-square"
                  alt="sv-square"
                  onClick={() => {
                    this.openStreetView(
                      this.props.item.latitude,
                      this.props.item.longitude,
                      this.props.item.property_address,
                      this.props.item.city_state_zip
                    );
                  }}
                />
              </Col>
            </Row>
            <Row
              onClick={() =>
                this.props.dealClick(
                  "ip",
                  this.props.item.deal_id,
                  this.props.item.total_violations,
                  this.props.item.is_equity,
                  this.props.item.deal_name,
                  "status-summary"
                )
              }
            >
              <Col md="2">
                <p className="small-title">Address</p>
                <p className="panel-info-value">
                  {this.props.item.property_address
                    ? this.props.item.property_address
                    : ""}
                </p>
                <p className="panel-info-value">
                  {this.props.item.city_state_zip
                    ? this.props.item.city_state_zip
                    : ""}
                </p>
              </Col>
              <Col md="2">
                <p className="small-title">Expected Closing Date</p>
                <p className="panel-info-value">
                  {this.props.item.expected_closing_date
                    ? this.props.item.expected_closing_date
                    : " - "}
                </p>
              </Col>
              <Col md="2">
                <p className="small-title">Property Type</p>
                <p className="panel-info-value">
                  {this.props.item.property_type
                    ? this.props.item.property_type
                    : " - "}
                </p>
              </Col>
              {this.props.item.is_equity ? (
                <Col md="2">
                  <p className="small-title">Fund Names</p>
                  <p className="panel-info-value">
                    {this.props.item.funds.length > 0
                      ? this.props.item.funds.join(", ")
                      : " - "}
                  </p>
                </Col>
              ) : (
                ""
              )}
              {this.props.item.is_equity ? (
                <Col md="2">
                  <p className="small-title deal-info-title">
                    Requested Equity Amount
                  </p>
                  <p className="panel-info-value">
                    {this.props.item.loan_amount
                      ? this.props.item.loan_amount
                      : " - "}
                  </p>
                </Col>
              ) : (
                ""
              )}
              {!this.props.item.is_equity ? (
                <Col md="2">
                  <p className="small-title">Main broker</p>
                  <p className="panel-info-value">
                    {this.props.item.broker_of_record
                      ? this.props.item.broker_of_record
                      : " - "}
                  </p>
                </Col>
              ) : (
                ""
              )}
              {!this.props.item.is_equity ? (
                <Col md="12">
                  <h3 className="quote-section orange-text">Loan Details</h3>
                  <Col md="2">
                    <p className="small-title">Bank</p>
                    <p className="panel-info-value">
                      {this.props.item.bank_name
                        ? this.props.item.bank_name
                        : " - "}
                    </p>
                  </Col>
                  <Col md="2">
                    <p className="small-title deal-info-title">Rate</p>
                    <p className="panel-info-value">
                      {this.props.item.rate ? this.props.item.rate : " - "}
                    </p>
                  </Col>
                  <Col md="2">
                    <p className="small-title deal-info-title">Loan amount</p>
                    <p className="panel-info-value">
                      {this.props.item.loan_amount
                        ? this.props.item.loan_amount
                        : " - "}
                    </p>
                  </Col>
                  <Col md="2">
                    <p className="small-title deal-info-title">
                      Annual payment
                    </p>
                    <p className="panel-info-value">
                      {this.props.item.yearly_amount
                        ? this.props.item.yearly_amount
                        : " - "}
                    </p>
                  </Col>
                  <Col md="2">
                    <p className="small-title deal-info-title">
                      Term / Amortization
                    </p>
                    <p className="panel-info-value">{`${
                      this.props.item.term ? this.props.item.term : "  -  "
                    } / ${
                      this.props.item.amortization
                        ? this.props.item.amortization
                        : "  -  "
                    }`}</p>
                  </Col>
                </Col>
              ) : (
                ""
              )}
            </Row>
            {JSON.parse(localStorage.getItem("data")).is_client &&
            !window.location.pathname.includes("contact") ? (
              <Row
                className={
                  this.props.type === "linked" ? "hidden" : "note-container"
                }
              >
                <Col md="10">
                  <p className="small-title deal-info-title">Note</p>
                  <div>
                    {this.props.item.notes ? (
                      <div
                        onClick={() =>
                          this.props.openEditNoteModal(
                            this.props.item.removal_id,
                            this.props.item.removal_type,
                            this.props.item.property_id,
                            this.props.item.notes
                          )
                        }
                      >
                        <p className="note padding-left-note">
                          {this.props.item.notes}&nbsp;
                          <img
                            src="/images/pencil.png"
                            id="pencil"
                            alt="pencil"
                          />
                        </p>
                        <p className="last-update">
                          <i>
                            last updated on{" "}
                            {moment(
                              new Date(this.props.item.notes_edited_on)
                            ).format("l")}
                          </i>
                        </p>
                      </div>
                    ) : (
                      <div
                        onClick={() =>
                          this.props.openAddNoteModal(
                            this.props.item.removal_id,
                            this.props.item.removal_type,
                            this.props.item.property_id
                          )
                        }
                        className="add-note"
                      >
                        <em>Add Note &nbsp;</em>
                        <i className="material-icons">add</i>
                      </div>
                    )}
                  </div>
                </Col>
              </Row>
            ) : (
              ""
            )}
            <Divider />
          </div>
          <Row>
            <Col md="12">
              <Row className="comps-row">
                <div onClick={this.compsClick.bind(this)}>
                  <div
                    className={
                      this.props.item.total_violations !== null
                        ? "border-right"
                        : ""
                    }
                  >
                    <p className="violation-value comps">
                      {formatTotalComps(this.props.item.TotalComps)}
                      <span>Comps</span>
                    </p>
                  </div>
                </div>
                <div
                  className="comps-row-violations"
                  onClick={() =>
                    this.props.dealClick(
                      "pw",
                      this.props.item.deal_id,
                      this.props.item.total_violations,
                      this.props.item.is_equity,
                      this.props.item.deal_name,
                      "violations"
                    )
                  }
                >
                  {this.props.item.total_violations !== null ? (
                    <div>
                      <p className="violation-value">
                        {this.props.item.total_violations}
                        <span>Violations</span>
                      </p>
                    </div>
                  ) : (
                    ""
                  )}
                  {this.props.item.total_complaints !== null ? (
                    <div>
                      <p className="violation-value">
                        {this.props.item.total_complaints}
                        <span>Complaints</span>
                      </p>
                    </div>
                  ) : (
                    ""
                  )}
                </div>
              </Row>
            </Col>
          </Row>
        </div>
      </div>
    );
  }
}
