import React from "react";
import { Navigation } from "../../lib";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Divider from "muicss/lib/react/divider";
import moment from "moment";
import { formatTotalComps } from "../../lib";
import encodeQueryData from "../common/encodeQueryData";

const google = window.google;

class RPDeal extends React.Component {
  dealClick(deal_id, property_id, violations, isEquity) {
    if (!window.location.pathname.includes("contact")) {
      this.props.setPageAndScrollPos(window.location.pathname, window.scrollY);
      let queryString = encodeQueryData({
        property_id,
        violations,
        isEquity,
        previous_page:
          encodeURIComponent(window.location.pathname) + window.location.search,
      });
      violations && violations >= 0
        ? Navigation.go(`/properties/rp/${deal_id}/violations?${queryString}`)
        : Navigation.go(
            `/properties/rp/${this.props.item.deal_id}/property-details?${queryString}`
          );
    } else {
      this.props.updateErrorToast(
        "No deal details or violations available for this deal"
      );
    }
  }

  compsClick() {
    sessionStorage.setItem("comps_inNYC", this.props.item.inNYC);
    this.props.setPageAndScrollPos(window.location.pathname, window.scrollY);
    this.props.submitCompsSearch(
      `${this.props.item.property_address || ""} ${
        this.props.item.property_city || ""
      } ${this.props.item.property_state || ""} ${
        this.props.item.property_zip || ""
      }`,
      `${this.props.item.property_type_id}`
    );
  }

  openStreetView(
    lat,
    long,
    property_address,
    property_city,
    property_state,
    property_zip
  ) {
    var sv = new google.maps.StreetViewService();
    sv.getPanorama(
      {
        location: {
          lat: parseFloat(lat),
          lng: parseFloat(long),
        },
        radius: 50,
      },
      (data, status) => {
        if (status === "OK") {
          this.props.setPageAndScrollPos(
            window.location.pathname,
            window.scrollY
          );
          Navigation.go(
            `/comp-sv?${encodeQueryData({
              latitude: lat,
              longitude: long,
              address:
                property_address +
                " " +
                property_city +
                " " +
                property_state +
                " " +
                property_zip,
            })}`
          );
        } else {
          this.props.updateErrorToast(
            "No street view available for this property"
          );
        }
      }
    );
  }

  render() {
    return (
      <div className="remaining-portfolio">
        <div className="panel mui-panel">
          <div className="panel-heading">
            <Col
              md="10"
              sm="10"
              xs="9"
              onClick={() => {
                this.dealClick(
                  this.props.item.deal_id,
                  this.props.item.property_id,
                  this.props.item.total_violations,
                  this.props.item.is_equity
                );
              }}
            >
              <p className="panel-info-value property-title">
                <b>
                  {this.props.item.property_address
                    ? this.props.item.property_address
                    : ""}{" "}
                  {this.props.item.property_address ? " - " : ""}
                  {this.props.item.property_city},{" "}
                  {this.props.item.property_state}{" "}
                  {this.props.item.property_zip}
                </b>
                <span
                  className={
                    this.props.item.deal_id ? "deal-tag primary-tag" : "hidden"
                  }
                >
                  {this.props.item.is_equity ? "Equity" : "Debt"}
                </span>
                <span
                  className="deal-tag"
                  style={{ background: this.props.item.tag_color }}
                >
                  {this.props.item.new_tag}
                </span>
              </p>
            </Col>
            <Col md="2" sm="2" xs="3">
              <i
                className={
                  this.props.type === "linked" ||
                  window.location.pathname.includes("contact")
                    ? "hidden"
                    : "material-icons deal-delete"
                }
                onClick={() => {
                  this.props.removeProperty(
                    this.props.item.removal_type,
                    this.props.item.removal_id
                  );
                }}
              >
                delete
              </i>
              <img
                src="/images/icons/street-view-square.png"
                className="street-view-square"
                alt="street-view-square"
                onClick={() => {
                  this.openStreetView(
                    this.props.item.latitude,
                    this.props.item.longitude,
                    this.props.item.property_address,
                    this.props.item.property_city,
                    this.props.item.property_state,
                    this.props.item.property_zip
                  );
                }}
              />
            </Col>
          </div>
          <Col
            md="12"
            onClick={() =>
              this.dealClick(
                this.props.item.deal_id,
                this.props.item.property_id,
                this.props.item.total_violations,
                this.props.item.is_equity
              )
            }
          >
            {this.props.item.is_equity ? (
              <Col md="2">
                <p className="small-title">Address</p>
                <p className="panel-info-value">
                  {this.props.item.property_address
                    ? this.props.item.property_address
                    : ""}
                </p>
                <p className="panel-info-value">
                  {this.props.item.city_state_zip
                    ? this.props.item.city_state_zip
                    : ""}
                </p>
              </Col>
            ) : (
              ""
            )}
            {this.props.item.is_equity ? (
              <Col md="2">
                <p className="small-title">Closed Date</p>
                <p className="panel-info-value">
                  {moment(this.props.item.activity_date).isValid()
                    ? moment(this.props.item.activity_date).format("MM/DD/YYYY")
                    : " - "}
                </p>
              </Col>
            ) : (
              ""
            )}
            <Col md="2">
              <p className="small-title">Property Type</p>
              <p className="panel-info-value">
                {this.props.item.property_type
                  ? this.props.item.property_type
                  : " - "}
              </p>
            </Col>
            {this.props.item.is_equity ? (
              <Col md="2">
                <p className="small-title">Fund Names</p>
                <p className="panel-info-value">
                  {this.props.item.funds.length > 0
                    ? this.props.item.funds.join(", ")
                    : " - "}
                </p>
              </Col>
            ) : (
              ""
            )}
            {this.props.item.is_equity ? (
              <Col md="2">
                <p className="small-title deal-info-title">
                  Requested Equity Amount
                </p>
                <p className="panel-info-value">
                  {this.props.item.loan_amount
                    ? this.props.item.loan_amount
                    : " - "}
                </p>
              </Col>
            ) : (
              ""
            )}
            {!this.props.item.is_equity ? (
              <div>
                <Col md="2">
                  <p className="small-title deal-info-title">Bank</p>
                  <p className="panel-info-value">
                    {this.props.item.bank_name
                      ? this.props.item.bank_name
                      : " - "}
                  </p>
                </Col>
                <Col md="2">
                  <p className="small-title deal-info-title">Rate</p>
                  <p className="panel-info-value">
                    {this.props.item.rate ? this.props.item.rate : " - "}
                  </p>
                </Col>
                <Col md="2">
                  <p className="small-title deal-info-title">Current balance</p>
                  <p className="panel-info-value">
                    {this.props.item.current_balance
                      ? this.props.item.current_balance
                      : " - "}
                  </p>
                </Col>
                <Col md="2">
                  <p className="small-title deal-info-title">
                    Prepayment penalty
                  </p>
                  <p className="panel-info-value">
                    {this.props.item.prepayment_penalty
                      ? this.props.item.prepayment_penalty
                      : " - "}
                  </p>
                </Col>
                <Col md="2">
                  <p className="small-title deal-info-title">
                    Next reduction date
                  </p>
                  <p className="panel-info-value">
                    {this.props.item.next_reduction
                      ? this.props.item.next_reduction
                      : " - "}
                  </p>
                </Col>
              </div>
            ) : (
              ""
            )}
          </Col>
          {JSON.parse(localStorage.getItem("data")).is_client &&
          !window.location.pathname.includes("contact") ? (
            <Row
              className={
                this.props.type === "linked" ? "hidden" : "note-container"
              }
            >
              <Col md="10">
                <p className="small-title deal-info-title">Note</p>
                <div>
                  {this.props.item.notes ? (
                    <div
                      onClick={() =>
                        this.props.openEditNoteModal(
                          this.props.item.removal_id,
                          this.props.item.removal_type,
                          this.props.item.property_id,
                          this.props.item.notes
                        )
                      }
                    >
                      <p className="note">
                        {this.props.item.notes}&nbsp;
                        <img
                          src="/images/pencil.png"
                          id="pencil"
                          alt="pencil"
                        />
                      </p>
                      <p className="last-update">
                        <i>
                          last updated on{" "}
                          {moment(
                            new Date(this.props.item.notes_edited_on)
                          ).format("l")}
                        </i>
                      </p>
                    </div>
                  ) : (
                    <div
                      onClick={() =>
                        this.props.openAddNoteModal(
                          this.props.item.removal_id,
                          this.props.item.removal_type,
                          this.props.item.property_id
                        )
                      }
                      className="add-note"
                    >
                      <em>Add Note &nbsp;</em>
                      <i className="material-icons">add</i>
                    </div>
                  )}
                </div>
              </Col>
            </Row>
          ) : (
            ""
          )}
          <Row className="clear">
            <Divider />
            <div className="comps-row">
              <div
                onClick={() => {
                  this.compsClick();
                }}
              >
                <div
                  className={
                    this.props.item.total_violations !== null
                      ? "border-right"
                      : ""
                  }
                >
                  <p className="violation-value comps">
                    {formatTotalComps(this.props.item.TotalComps)}
                    <span>Comps</span>
                  </p>
                </div>
              </div>
              <div
                className="comps-row-violations"
                onClick={() => {
                  this.dealClick(
                    this.props.item.deal_id,
                    this.props.item.property_id,
                    this.props.item.total_violations,
                    this.props.item.is_equity
                  );
                }}
              >
                {this.props.item.total_violations !== null ? (
                  <div>
                    <p className="violation-value">
                      {this.props.item.total_violations}
                      <span>Violations</span>
                    </p>
                  </div>
                ) : (
                  ""
                )}
                {this.props.item.total_complaints !== null ? (
                  <div>
                    <p className="violation-value">
                      {this.props.item.total_complaints}
                      <span>Complaints</span>
                    </p>
                  </div>
                ) : (
                  ""
                )}
              </div>
            </div>
          </Row>
        </div>
      </div>
    );
  }
}

export default RPDeal;
